import {
	cloneFn
} from '@/tools/tools';


// 
//  data 
function handleDataFn(data) {
	let resultList = []; //  

	cloneFn(data).forEach(item => {
		if (resultList.length) { // 
			let endIsPushFlag; // push
			resultList.forEach(it => {

				if (it.gdId == item.gb_id) { // resultList id
					endIsPushFlag = false;

					if (it.userInfo.length < 2) {
						it.userInfo.push({ // push
							headimgurl: item.headimgurl,
							nickName: item.nickname,
						})
					}

				} else { // resultList 
					endIsPushFlag = true;
				}
			})
			if (endIsPushFlag) { //    push
				resultList.push({
					gdId: item.gb_id, // id
					diffNum: item.diffNum,
					endTime: item.end_time,
					userInfo: [{
						headimgurl: item.headimgurl,
						nickName: item.nickname,
					}]
				})
			}
		} else { // resultList   push
			resultList.push({
				gdId: item.gb_id, // id
				diffNum: item.diffNum,
				endTime: item.end_time,
				encrypt: item.encrypt,
				userInfo: [{
					headimgurl: item.headimgurl,
					nickName: item.nickname,
				}]
			})
		}
	})
	/* return [...resultList,...resultList,...resultList,...resultList,...resultList,...resultList,...resultList]; */ //  return 
	return resultList;
}


export {
	handleDataFn
}
