import {
	num2zeroStr,
	transNumFn
} from '@/tools/tools.js';


// 



/*
	@description  xx:xx:xx ::
	@param { Number } allMinute 
	@return { String } xx:xx:xx ::
*/
function allMinute2Time(allMinute) {
	let newDate = new Date();
	let hour = Math.floor(allMinute / 60);
	let minute = allMinute % 60;
	let second = 60 - newDate.getSeconds();
	return {
		hour: num2zeroStr(hour),
		minute: num2zeroStr(minute),
		second: num2zeroStr(second),
	};
}



/*
	@description  (12:00)(13:00)  tipTitle     ()
	@param { String } miaoshaStartTime (12:00)
	@param { String } miaoshaEndTime (13:00)
	@return { Object } 
		{
			tipTitle: '', //    
			hour: '01', //   
			minute: '02', // 
			second: '03', // 
		}
*/
function getMiaoshaTimeOption(miaoshaStartTime, miaoshaEndTime) {
	let newDate = new Date(); // 
	let newTimeHour = newDate.getHours(); //  20
	let newTimeMinute = newDate.getMinutes(); //  00
	let newTimeAllMinute = newTimeHour * 60 + newTimeMinute; // 

	let miaoshaStartTimeArr = miaoshaStartTime.split(':'); //  20:00
	let miaoshaStartTimeHour = transNumFn(miaoshaStartTimeArr[0]); //  20
	let miaoshaStartTimeMinute = transNumFn(miaoshaStartTimeArr[1]); //  00
	let miaoshaStartTimeAllMinute = miaoshaStartTimeHour * 60 + miaoshaStartTimeMinute; // 

	let miaoshaEndTimeArr = miaoshaEndTime.split(':'); //  20:00
	let miaoshaEndTimeHour = transNumFn(miaoshaEndTimeArr[0]); //  20
	let miaoshaEndTimeMinute = transNumFn(miaoshaEndTimeArr[1]); //  00
	let miaoshaEndTimeAllMinute = miaoshaEndTimeHour * 60 + miaoshaEndTimeMinute; // 

	let allMinute; // 
	let tipStr; //   (/)
	if (miaoshaStartTimeAllMinute < newTimeAllMinute && miaoshaEndTimeAllMinute > newTimeAllMinute) {
		//      xxx
		allMinute = miaoshaEndTimeAllMinute - newTimeAllMinute;
		tipStr = '';
	} else if (miaoshaStartTimeAllMinute > newTimeAllMinute) {
		//      xxx
		allMinute = miaoshaStartTimeAllMinute - newTimeAllMinute;
		tipStr = '';
	} else if (miaoshaEndTimeAllMinute < newTimeAllMinute) {
		//      xxx
		allMinute = 24 * 60 - newTimeAllMinute + miaoshaStartTimeAllMinute;
		tipStr = '';
	}
	return {
		tipTitle: tipStr,
		...allMinute2Time(allMinute),
	}
}
export {
	getMiaoshaTimeOption
}
