//  
function addSpecFilterItemActAttrFn(specFilterItem) {
	let cloneSpecFilterItem = cloneFn(specFilterItem);
	cloneSpecFilterItem.forEach(item => {
		item.item.forEach((i, k) => {
			if (k == 0) {
				i.active = true;
			} else {
				i.active = false;
			}
		})
	})
	return cloneSpecFilterItem;
}
