let validateFn = {
	name(title,str){
		if(str.length > 0 && str.length <= 20){
			return {
				validate:true
			}
		}
		return {
			validate: false,
			msg:`${title}1~20`
		}
	},
	tel(tel){
		if(checkTelReg.test(tel)){
			return {
				validate:true
			}
		}
		return {
			validate: false,
			msg:``
		}
	}
}


