//  

import {
	checkAvatarPath
} from '@/tools/tools'

const store = {
	state: {


		userInfoAvatar: '', // 
		userInfoNickName: '', // 
		userInfoLv: 0, // 
		userInfoMoney: 0, // 
		userInfoCoupon: 0, // 
		userInfoMc: 0, // 
		userInfoIntegral: 0, // 
		userInfoMobile: '', // 
		userInfoEncrypt: '', // 


		isVip: false, // vip
		vipName: '', //     
		vipTes: '', //   
		vipEndTime: '', //     


		laBaMsg: '', //      user_vip_card_cue
		vipIcon: '', // V   user_vip_card_icon
		vipTitle: '', // V  vip_eco
		vipBtnText: '', // V   vip_button
		vipIconList: [], // 


		orderNumUn: 0, // -
		orderNumIng: 0, // -
		orderNumEd: 0, // -

		bannerList: [], // 

	},
	mutations: {
		//  
		setPage(state, arg) {

			let {
				laBaMsg, //      user_vip_card_cue
				vipIcon, // V   user_vip_card_icon
				vipTitle, // V  vip_eco
				vipBtnText, // V   vip_button
				vipIconList, // 

				bannerList, // 
			} = arg;
			state.RobFoodmarket = arg


			state.laBaMsg = laBaMsg; //      user_vip_card_cue
			state.vipIcon = vipIcon; // V   user_vip_card_icon
			state.vipTitle = vipTitle; // V  vip_eco
			state.vipBtnText = vipBtnText; // V   vip_button
			state.vipIconList = vipIconList; // 

			state.bannerList = bannerList; // 


		},
		//  +
		setUserInfoAndPage(state, arg) {
			let {
				userInfoAvatar, // 
				userInfoNickName, // 
				userInfoLv, // 
				userInfoMoney, // 
				userInfoCoupon, // 
				userInfoMc, // 
				userInfoIntegral, // 
				userInfoMobile, // 
				userInfoEncrypt, // 


				isVip, // vip
				vipName, //     
				vipTes, //   
				vipEndTime, //     


				laBaMsg, //      user_vip_card_cue
				vipIcon, // V   user_vip_card_icon
				vipTitle, // V  vip_eco
				vipBtnText, // V   vip_button
				vipIconList, // 


				orderNumUn, // -
				orderNumIng, // -
				orderNumEd, // -

				bannerList, // 

			} = arg;


			state.userInfoAvatar = checkAvatarPath(userInfoAvatar); // 
			state.userInfoNickName = userInfoNickName; // 
			state.userInfoLv = userInfoLv; // 
			state.userInfoMoney = userInfoMoney; // 
			state.userInfoCoupon = userInfoCoupon; // 
			state.userInfoMc = userInfoMc; // 
			state.userInfoIntegral = userInfoIntegral; // 
			state.userInfoMobile = userInfoMobile; // 
			state.userInfoEncrypt = userInfoEncrypt; // 


			state.isVip = isVip; // vip
			state.vipName = vipName; //     
			state.vipTes = vipTes; //   
			state.vipEndTime = vipEndTime; //     


			state.laBaMsg = laBaMsg; //      user_vip_card_cue
			state.vipIcon = vipIcon; // V   user_vip_card_icon
			state.vipTitle = vipTitle; // V  vip_eco
			state.vipBtnText = vipBtnText; // V   vip_button
			state.vipIconList = vipIconList; // 


			state.orderNumUn = orderNumUn; // -
			state.orderNumIng = orderNumIng; // -
			state.orderNumEd = orderNumEd; // -

			state.bannerList = bannerList; // 
		}
	},
	actions: {},
	getters: {},
};

export default store;
